import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import { Provider } from "react-redux";
import ConfigStore from "./components/redux/ConfigStore";

ReactDOM.render(
  <React.StrictMode>
    <Provider store={ConfigStore}>
      <App />
    </Provider>
  </React.StrictMode>,
  document.getElementById("root")
);
